import React from "react";

// Utils
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLink } from "@fortawesome/free-solid-svg-icons";
import { NavLink } from "react-router-dom";

// Components functions
const Link = (props) => {
  if (props.refresh) {
    return (
      <a href={props.link} className="tp-link">
        <FontAwesomeIcon icon={faLink} />
        {props.value}
      </a>
    );
  } else {
    return (
      <NavLink to={props.link} className="tp-link">
        <FontAwesomeIcon icon={faLink} />
        {props.value}
      </NavLink>
    );
  }
};

const LinksBlock = (props) => {
  let links = props.links.map((item) => (
    <Link
      key={item.id}
      value={item.value}
      link={item.link}
      refresh={item.refresh}
    />
  ));
  return <div className="tp-links_block">{links}</div>;
};

const Sublink = (props) => {
  return (
    <a href={props.link} className="tp-sublink">
      {props.value}
    </a>
  );
};

const SublinksBlock = (props) => {
  let sublinks = props.sublinks.map((item) => (
    <Sublink key={item.id} value={item.value} link={item.link} />
  ));
  return <div className="tp-sublinks_block">{sublinks}</div>;
};

const Text = (props) => {
  if (!props.id) props.id = "";

  let content = "";

  props.value.forEach((t) => {
    content += `${t}\n`;
  });

  return (
    <p className="tp-text" id={props.id}>
      {content}
      <br />
    </p>
  );
};

export { Link, LinksBlock, Sublink, SublinksBlock, Text };
